// import classnames from "classnames"
import { Button } from "reactstrap"

import { useScenario } from "../contexts/ScenarioContext"

export default function RevealTableRow({label, category, values}) {
    const {logPatientStatusRequest } = useScenario()
    // const [show, setShow] = useState(false)
    // const toggle = () => setShow(!show)
    // const visible = true

    const clickHandler = (value)  => {
        logPatientStatusRequest(category, label, value)
    }

    return (
        <tr>
            <th>{label} </th>
            {
                values.map((value, index) => (
                    <td key={`${category}-${label}-${value}` }>
                        <span >
                            {value} &nbsp;
                            <Button color="success" size="sm" onClick={() => clickHandler(value) } >
                                Requested
                            </Button>
                        </span>
                    </td>
                ))
            }
        </tr>
    )

}